@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
* {
  margin: 0;
}

body {
  margin: 0;
  font-family: 'Open Sans', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}
.app__main {
  display: -webkit-flex;
  display: flex;
}

.app__login {
  width: 100vw;
  height: 100vh;
  display: grid;
  place-items: center;
}

.app__login>button {
  border: none;
  font-size: 24px;
  background-color: rgb(67, 130, 244);
  color: white;
  padding: 10px 20px;
  border-radius: 6px;
  transition: all 0.2s;
}

.app__login>button:hover {
  cursor: pointer;
  background-color: rgb(49, 94, 179);
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
.header {
  display: -webkit-flex;
  display: flex;
  height: 60px;
  border-bottom: 1px solid rgb(219, 219, 219);
  width: 100vw;
}

.header>div {
  padding: 12px;
}

.header__logo {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items: center;
          align-items: center;
}

.header__logo>img {
  height: 100%;
  object-fit: contain;
}

.header__logo>span {
  color: gray;
  font-size: 20px;
  font-weight: 500;
  margin-left: 16px;
}

.header__searchContainer {
  -webkit-flex: 1 1;
          flex: 1 1;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  padding: 8px;
}

.header__searchBar {
  width: 45%;
  height: 120%;
  border-radius: 6px;
  background-color: rgb(237, 237, 237);
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  padding: 0 8px;
}

.MuiSvgIcon-root {
  color: rgb(82, 82, 82);
}

.header__searchBar>input {
  -webkit-flex: 1 1;
          flex: 1 1;
  height: 60%;
  font-size: 16px;
  color: lightgray;
  background: none;
  border: none;
  margin-left: 12px;
}

.header__searchBar>input:focus {
  outline: none;
  color: black;
}

.header__icons {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  margin-right: -30px;
}

.header__icons .MuiSvgIcon-root {
  font-size: 28px;
